.header {
	position: absolute;
	z-index: 100;
	top: 0;
	left: 0;
	right: 0;
	color: $white;
	&.is-fixed {
		position: fixed;
		color: $dark;
		background: $white;
		box-shadow: inset 0 -1px 0 0 rgba(204, 204, 204, 0.5);
		.nav {
			&__btn {
				color: $dark;
				background: $green;
				box-shadow: none; } } }
	&.is-back {
		animation: show 0.3s ease-in-out forwards reverse; }
	&__inner {
		padding-top: 30px;
		padding-bottom: 30px; } }

.logo {
	float: left;
	padding: 5px 0;
	color: inherit;
	font-size: 0;
	.icon {
		color: inherit;
		fill: $green;
		font-size: 28px; } }

.nav {
	float: right;
	&__item {
		display: inline-block;
		vertical-align: middle;
		&.is-active {
			font-weight: 700; }
		&:not(:first-child) {
			margin-left: 62px; } }
	&__link,
	&__btn {
		display: inline-block;
		vertical-align: top;
		font-family: $montserrat;
		@include f(13, 16px, 0.5);
		text-transform: uppercase; }
	&__link {
		padding: 11px 0;
		color: inherit;
		opacity: 0.9; }
	&__btn {
		padding: 10px 27px;
		border: 1px solid $green;
		border-radius: 29px;
		color: $green;
		background: none;
		box-shadow: 0 8px 27px 0 rgba(0, 0, 0, 0.26);
		transition: color 0.15s ease, background 0.15s ease, box-shadow 0.15s ease;
		@include hover {
			color: $dark;
			background: $green; } } }
